.navbar {
  margin: auto;
  min-height: 60px;
  background: #fefefe;
  border-color: $light-color;

  a {
    color: #9c9c9c;
    font-size: 10px;
    letter-spacing: 2px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-transform: uppercase;
  }

  li a {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  li a:hover {
    color:$primary-color !important;
  }
}